.menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.menu-button__div {
    width: 50vw;
}

.menu-button__div-img {
    height: 100%;
    width: 100%;
    object-position: center;
}

.menu-button__div-img {
    position: relative;
    object-fit: fill;
    background-repeat: no-repeat;
}

.menu-button__div p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) ;
    color: white;
    font-weight: 700;
}